<template>
  <div>
    <v-sheet color="rgba(0,0,0,0.6)" min-height="600">
      <v-container>
        <v-row>
          <v-col cols="12" class="text-center white--text">
            <h1>{{ Tipo }}</h1>
          </v-col>
        </v-row>
        <v-row>
          <v-row align-content="center" align="center" justify="center">
            <v-col cols="12" md="6" class="text-center" align-self="center">
              <v-chip-group
                v-model="selection_Fecha"
                active-class="light-green--text"
                mandatory
                dark
                
              >
                <v-chip value="1"> Un Día </v-chip>
                <v-chip value="0"> Quincenal </v-chip>
                <v-chip value="2"> Rango De Fechas </v-chip>
              </v-chip-group>
            </v-col>
            <v-col cols="12" md="6" v-if="selection_Fecha == 0">
              <v-select
                v-model="Selecion_Quincena"
                :items="Lista_Quincena"
                :menu-props="{ maxHeight: '400' }"
                label="Seleccione la quincena"
                hint="Selecciona la quincena"
                persistent-hint
                @change="Traer_Faltas_Justificadas()"
                dark
              ></v-select>
            </v-col>
            <v-col cols="12" md="6" v-else-if="selection_Fecha == 1">
              <v-dialog
                ref="dialog"
                v-model="modal"
                :return-value.sync="Dia"
                persistent
                width="290px"
                dark
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="Dia"
                    label="Seleccione el día"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                    dark
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="Dia"
                  scrollable
                  :max="hoy"
                  
                >
                  <v-spacer></v-spacer>
                  <v-btn text color="primary" @click="modal = false">
                    Cancelar
                  </v-btn>
                  <v-btn text color="primary" @click="$refs.dialog.save(Dia); Traer_Faltas_Justificadas()" v-if="Dia">
                    OK
                  </v-btn>
                </v-date-picker>
              </v-dialog>
            </v-col>
            <v-col cols="12" md="6" v-else>
              <v-dialog
                ref="dialog2"
                v-model="modal2"
                :return-value.sync="Dias"
                persistent
                width="290px"
                dark
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="Dias"
                    label="Seleccione el rango de días"
                    prepend-icon="mdi-calendar"
                    v-bind="attrs"
                    v-on="on"
                    dark
                  ></v-text-field>
                </template>
                <v-date-picker v-model="Dias" scrollable range :max="hoy">
                  <v-spacer></v-spacer>
                  <v-btn text color="primary" @click="modal2 = false">
                    Cancel
                  </v-btn>
                  <v-btn text color="primary" @click="$refs.dialog2.save(Dias);Traer_Faltas_Justificadas()" v-if="Dias.length == 2">
                    OK
                  </v-btn>
                </v-date-picker>
              </v-dialog>
            </v-col>
          </v-row>
        </v-row>
        <v-row class="white--text text-center" v-if="seleccion_Completa">
          <v-col md="8">
            <v-card color="rgba(0,0,0,0.8)">
              <v-card-title>
                <v-col cols="12" md="8">
                  <v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="Buscar"
                    single-line
                    hide-details
                    dark
                  ></v-text-field>
                </v-col>
              </v-card-title>
              <v-data-table
                :headers="headers"
                :items="docentes"
                :search="search"
                dark
              >
              </v-data-table>
            </v-card>
          </v-col>
          <v-col cols="12" md="4" v-if="docentes.length > 0"> 
            <v-row no-gutters>
              <v-col cols="12">
                <v-switch
                  v-model="PDF"
                  label="Crear PDF"
                  color="success"
                  value="Crear PDF"
                  inset
                  dark
                ></v-switch>
              </v-col>

              <v-col cols="12" class="white--text" v-if="PDF && docentes.length > 0">
                <h2>Generar Reporte En PDF</h2>

                <p></p>
                <div>
                  <v-switch
                    v-model="Todos"
                    label="Crear PDF de todos"
                    color="success"
                    inset
                    dark
                  ></v-switch>
                </div>
                <v-divider></v-divider>
                <div v-if="!Todos">
                  <v-btn
                    @click="Selec_Prof = true"
                    elevation="3"
                    outlined
                    rounded
                    color="blue"
                    >Ver Lista</v-btn
                  >

                  <v-dialog
                    v-model="Selec_Prof"
                    persistent
                    :overlay="false"
                    max-width="500px"
                    transition="dialog-transition"
                  >
                    <v-card dark elevation="3">
                      <v-card-title primary-title>
                        Seleccione a los docentes
                      </v-card-title>
                      <v-card-text>
                        <v-select
                          v-model="Profesores_Seleccionados"
                          :items="Lista_Profesores"
                          :menu-props="{ maxHeight: '400' }"
                          label="Seleccione los docentes"
                          multiple
                          dark
                          persistent-hint
                          width.min="300"
                        >
                        </v-select>
                      </v-card-text>

                      <v-card-actions
                        v-if="Profesores_Seleccionados.length > 0"
                        class="text-center"
                      >
                        <v-col cols="12">
                          <v-btn text color="red" @click="Selec_Prof = false">
                            Cerrar
                          </v-btn>
                        </v-col>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </div>
                <div>
                  <v-divider></v-divider>
                  <p></p>
                  <v-btn
                    elevation="3"
                    color="blue lighten-4"
                    rounded
                    outlined
                    block
                    @click="Crear_PDF()"
                    >Crear PDF</v-btn
                  >
                </div>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-container>
    </v-sheet>
  </div>
</template>
<script>
import axios from "axios";
import { mapMutations, mapState } from "vuex";
import jsPDF from "jspdf";
import "jspdf-autotable";
export default {
  mounted() {
    //this.Falta_Justificadas()
    this.Quincenas()
  },
  data() {
    return {
      Periodo:"",
      Tipo:"Asistencias",
      nombre_doc: "",
      Cabezera: ["No EMPLEADO","DOCENTE", "FECHA","HORAS FALTANTES","HORAS EXTRAS"],
      Cuerpo: [],
      Profesores_Seleccionados: [],
      Selec_Prof: false,
      Lista_Profesores: [],
      Lista_Profesores_2: [],
      selection_Fecha: 1,
      Texto: "",
      Todos: true,
      PDF: false,
      hoy: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      modal: false,
      modal2: false,
      Dia: "",
      Dias: [
        new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
          .toISOString()
          .substr(0, 10),
      ],
      Objeto_Quincenas: [],
      Lista_Quincena: [],
      Selecion_Quincena: "",
      search: "",
      Justificacion: {
        Falta: "Enfermedad",
        Dia: "01-03-2021",
        noEstatus: -1,
        Estatus: "Negado",
      },
      info: false,      
      headers: [
        {
          text: "No EMPLEADO",
          align: "start",
          value: "No_Empleado",
        },
        {
          text: "DOCENTE",          
          value: "Nombre",
        },
        { text: "FECHA ", value: "Fecha" },
        { text: "HORAS FALTANTES", value: "Horas_F"}, 
        { text: "HORAS Extras", value: "Horas_E"},        
        
      ],      
      docentes: [],
      Motivos:null
    };
  },
  computed: {
    ...mapState(["ip"]),
  },
  watch: {
    
   
    
    Todos: function (value) {
      if (value == false) {
        this.Selec_Prof = true;
      }
    },
  },
  methods: {
      ...mapMutations([
      "Modificar_cargar",
      "Checar_Token",
      "Salir",
      "Modificar_Informacion",
      "Modificar_Espera",
      "Modificar_Correcto",
      "Modificar_Datos",
      "Modificar_Boton",
      "Modificar_ruta",
    ]),
    seleccion_Completa() {
      if (this.selection_Fecha == 0) {
        return this.Selecion_Quincena != "";
      } else if (this.selection_Fecha == 1) {
        return this.Dia != "";
      } else {
        return this.Dias.length >= 2;
      }
    },
    async Traer_Faltas_Justificadas(){
        this.docentes = []
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Cargando Faltas");
      this.Modificar_Espera(true);
      
      try {
        let u = {};
      if (this.selection_Fecha == 0) {
          const aux = this.Objeto_Quincenas.find(
            (element) => element.quincena == this.Selecion_Quincena
          );
          u = {
            token: localStorage.getItem("Token"),
            fechaI: aux.primer_dia.toISOString().substr(0, 10),
            fechaF: aux.ultimo_dia.toISOString().substr(0, 10)
          };
        } else if (this.selection_Fecha == 1) {
          u = {
            token: localStorage.getItem("Token"),
            fechaI: this.Dia,
            fechaF: this.Dia,
          };
        } else {
          if (this.Dias[0] > this.Dias[1]) {
            u = {
              token: localStorage.getItem("Token"),
              fechaI: this.Dias[1],
              fechaF: this.Dias[0],
            };
          } else {
            u = {
              token: localStorage.getItem("Token"),
              fechaI: this.Dias[0],
              fechaF: this.Dias[1],
            };
          }
        }
        const json = JSON.stringify(u);      
//      console.log(json);
        // let res = await axios.post(
        //   "http://" + this.ip + "/api/jefe_departamento/Falta_Justificada",
        //   json,
        //   {
        //     headers: {
               // Overwrite Axios's automatically set Content-Type
        //       "Content-Type": "application/json",
        //     },
        //   }
        // );
        let res = await axios.post(
          "http://" + this.ip + "/api/capital_humano/asistencias-docentes-rango",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        
        if (!this.error && !res.data.Error) {
          let Meses = [
          "Enero",
          "Febrero",
          "Marzo",
          "Abril",
          "Mayo",
          "Junio",
          "Julio",
          "Agosto",
          "Septiembre",
          "Octubre",
          "Noviembre",
          "Diciembre",
        ];
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Se Cargaron los datos");          
          this.Modificar_Boton(true);     
          const DOCENTES =[]     
          let i = 0
          res.data.Datos.forEach(element => {
            DOCENTES.push({
              No_Empleado: element.No_Empleado,
              Nombre: element.Nombre,
              Fecha: element.Fecha.substring(8,10) +" de "+ Meses[ parseInt(element.Fecha.substring(5,7),10)-1] +" del "+ element.Fecha.substring(0,4),
              Horas_F: element.Horas_F+" hrs",
              Horas_E: element.Horas_F+" hrs",           
              
              id:i
            });
            i++;            
          });
          this.docentes = DOCENTES;
          this.Cargar_docentes_para_seleccion();
        } else if (res.data.Error) {
          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos(res.data.status);
          this.Modificar_Boton(true);
          
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        
        console.log(error);
      }
    },
    async Traer_Faltas() {
      try {
        let u = {};
        
        if (this.selection_Fecha == 0) {
          const aux = this.Objeto_Quincenas.find(
            (element) => element.quincena == this.Selecion_Quincena
          );
          u = {
            id: this.No_JD,
            fechaI: aux.primer_dia.toISOString().substr(0, 10),
            fechaF: aux.ultimo_dia.toISOString().substr(0, 10),
          };
        } else if (this.selection_Fecha == 1) {
          u = {
            id: this.No_JD,
            fechaI: this.Dia,
            fechaF: this.Dia,
          };
        } else {
          if (this.Dias[0] > this.Dias[1]) {
            u = {
              id:  this.No_JD,
              fechaI: this.Dias[1],
              fechaF: this.Dias[0],
            };
          } else {
            u = {
              id: this.No_JD,
              fechaI: this.Dias[0],
              fechaF: this.Dias[1],
            };
          }
        }
        const json = JSON.stringify(u);
        
        let res = await axios
          .post(
            "http://" + this.ip + "/api/jefe_departamento/faltas-docentes",
            json,
            {
              headers: {
                // Overwrite Axios's automatically set Content-Type
                "Content-Type": "application/json",
              },
            }
          )
          .catch((error) => {
            console.log(error);
          });
        // this.desserts.apend
          
        const FALTAS = [];        
        if (res.data.length >= 1) {
          //this.Periodo = res.data[0].Periodo;
          this.Periodo = "2021-2 "
          res.data.forEach((element) => {
            FALTAS.push({
              Nombre: element.Nombre,
              Falta: "Falta",
              Dia: element.Fecha.substr(0, 10),
            });
          });
          this.docentes = FALTAS;
          this.Cargar_docentes_para_seleccion();
        }

        
      } catch (error) {
        console.log(error);
      }
    },
    
    Quincenas() {
      if (this.Lista_Quincena.length == 0) {
        let Meses = [
          "Enero",
          "Febrero",
          "Marzo",
          "Abril",
          "Mayo",
          "Junio",
          "Julio",
          "Agosto",
          "Septiembre",
          "Octubre",
          "Noviembre",
          "Diciembre",
        ];
        //let hoy = new Date();
        let hoy = new Date('2021/12/31');
        let Ultimo_mes = hoy.getMonth();
        let mes = hoy.getMonth();

        if (hoy.getDate() < 15) {
          Ultimo_mes--;
        }
        if (Ultimo_mes == -1) {
          return [];
        } else {
          for (let i = 0; i <= Ultimo_mes; i++) {
            let primero = new Date(hoy.getFullYear(), i, 1);
            let ultimo = new Date(hoy.getFullYear(), i + 1, 0);
            let quince = new Date(hoy.getFullYear(), i, 15);
            let siguiente = new Date(hoy.getFullYear(), i, 16);
            if (i != mes) {
              this.Objeto_Quincenas.push({
                quincena: "1a Quincena de " + Meses[i],
                primer_dia: primero,
                ultimo_dia: quince,
              });
              this.Lista_Quincena.push("1a Quincena de " + Meses[i]);
              this.Lista_Quincena.push("2da Quincena de " + Meses[i]);
              this.Objeto_Quincenas.push({
                quincena: "2da Quincena de " + Meses[i],
                primer_dia: siguiente,
                ultimo_dia: ultimo,
              });
            } else {
              this.Objeto_Quincenas.push({
                quincena: "1a Quincena de " + Meses[i],
                primer_dia: primero,
                ultimo_dia: quince,
              });
              this.Lista_Quincena.push("1a Quincena de " + Meses[i]);
            }
          }
        }
      }
      
    },
    Cargar_docentes_para_seleccion() {
      this.docentes.forEach((element) => {
        if (this.Lista_Profesores.length == 0) {
          this.Lista_Profesores.push(element.Nombre);
        } else if (!this.Lista_Profesores.includes(element.Nombre)) {
          this.Lista_Profesores.push(element.Nombre);
        }
      });
    },
    Cargar_Datos_Para_PDF() {
      this.Cuerpo = [];
      if (this.Todos) {
        this.docentes.forEach((element) => {
              
          this.Cuerpo.push([element.No_Empleado, element.Nombre, element.Fecha,element.Horas_F, element.Horas_E]);
        });
      } else {
        
        this.docentes.forEach((element) => {
          if (this.Profesores_Seleccionados.includes(element.Nombre)) {
            this.Cuerpo.push([element.No_Empleado, element.Nombre, element.Fecha,element.Horas_F, element.Horas_E]);
          }
        });
      }
      this.Texto = "";
      let Meses = [
          "Enero",
          "Febrero",
          "Marzo",
          "Abril",
          "Mayo",
          "Junio",
          "Julio",
          "Agosto",
          "Septiembre",
          "Octubre",
          "Noviembre",
          "Diciembre",
        ];
      if (this.selection_Fecha == 0) {
        this.Texto = this.Periodo + " " + this.Selecion_Quincena + ".";
      } else if (this.selection_Fecha == 1) {
        this.Texto = "Del Dia: " + this.Dia.substring(8,10)+" de "+Meses[parseInt(this.Dia.substring(5,7),10)-1]+" del "+  this.Dia.substring(0,4) + ".";
      } else {
        if (this.Dias[0] < this.Dias[1]) {
          this.Texto = "Del Dia: "+ this.Dias[0].substring(8,10)+" de "+Meses[parseInt(this.Dias[0].substring(5,7),10)-1]+" del "+  this.Dias[0].substring(0,4)+ " al " + this.Dias[1].substring(8,10)+" de "+Meses[parseInt(this.Dias[1].substring(5,7),10)-1]+" del "+  this.Dias[1].substring(0,4) + ".";
        } else {
          this.Texto = "Del Dia: " + this.Dias[1].substring(8,10)+" de "+Meses[parseInt(this.Dias[1].substring(5,7),10)-1]+" del "+  this.Dias[1].substring(0,4) + " al " + this.Dias[0].substring(8,10)+" de "+Meses[parseInt(this.Dias[0].substring(5,7),10)-1]+" del "+  this.Dias[0].substring(0,4) + ".";
        }
      }
      this.nombre_doc = this.Texto.replace(" ", "");
    },
    Crear_PDF() {
      this.Cargar_Datos_Para_PDF();
      var ipn =
        "data:image/png;base64,";
      var imgdata =
        "data:image/png;base64,";
      var doc = new jsPDF();
      doc.autoTable({
        margin: { top: 50 },
        startY: 60,
        head: [this.Cabezera],
        body: this.Cuerpo,
        didDrawPage: function () {
          // Header
          doc.addImage(ipn, "PNG", 0, 10, 40, 30);
          doc.addImage(imgdata, "PNG", 158, 10, 40, 30);
          doc.setFontSize(18);
          doc.setFont("Times", "bold");
          doc.text("INSTITUTO POLITÉCNICO NACIONAL", 35, 20);
          doc.text("ESCUELA SUPERIOR DE CÓMPUTO", 39, 26);
          doc.setFont("Times", "BoldItalic");
          doc.setFontSize(15);
          doc.text("SUBDIRECCIÓN ADMINISTRATIVA", 50, 33); //#282828
          doc.setFontSize(20);
        },
      });
      doc.setPage(1);
      doc.setTextColor("#005CFE");

      doc.setFont("Times", "bold");
      doc.text(this.Tipo, 60, 45);

      doc.setTextColor(0);
      doc.setFontSize(12);
      doc.text(this.Texto, 40, 55);

      doc.save(this.nombre_doc + ".pdf");
    },
  },

}
</script>